.splashContainer {
  position: relative;
  z-index: 1;
  margin-top: 80px !important;
  margin-bottom: 100px !important;

  @media screen and (min-width: 1080px) {
    min-height: 500px;
    padding-right: 700px !important;
  }
  @media screen and (min-width: 1250px) {
    min-height: 500px;
    padding-right: 500px !important;
  }

  h1 {
    // color: #2A2A2A;
    color: white;
    opacity: 0.03;

    font-size: 6.875rem;
    line-height: 5rem;
    font-weight: 400;

    @media screen and (max-width: 1079px) {
      opacity: 0;
    }
  }

  h2 {
    @media screen and (max-width: 1079px) {
      margin-top: 20vw;
    }
  }

  h6 {
    color: rgb(167, 167, 167);
    font-size: 0.9rem;
    font-weight: 400;
    margin-bottom: 30px;
  }

  .buttonContainer {
    margin-top: 40px;
    @media screen and (max-width: 1079px) {
      width: 100%;
      display: flex;
      justify-content: center;
      flex-direction: column;
    }

    p {
      width: 100%;
      max-width: 350px;
      color: #a7a7a7;
      margin-top: 1rem;
      font-size: 0.9rem;

      @media screen and (max-width: 991px) {
        text-align: center;
        max-width: none;
      }
    }
  }

  button {
    border-radius: 5px;
    margin-top: 10px;
    background: linear-gradient(108deg, #58BFB8, #83D8A8);
    border: none;

    transition: transform 0.3s ease;

    &:hover {
      transform: scale(1.1);
    }
    &:active {
      transition: transform 0.1s;
      transform: scale(1);
    }

    i {
      margin-left: 20px;
    }

    span {
      text-shadow: 0 3px 5px rgba(black, 0.24);
      font-weight: bold;
    }
  }
}

.mainLogo {
  max-width: 241px;
  height: auto;

  @media screen and (max-width: 1079px) {
    opacity: 0;
  }
}

.logoContainer {
  display: flex;
}


.text {
  white-space: pre-wrap;
  font-size: 1rem;
  margin-bottom: 15px;
}